export default {
	//import引入的组件需要注入到对象中才能使用
	components: {},
	data() {
		//这里存放数据
		return {
			// 默认选中
			indexs: null,
			category: [],
			goods: [],
			names: ''
		};
	},
	//监听属性 类似于data概念
	computed: {},
	//监控data中的数据变化
	watch: {},
	//方法集合
	methods: {
		iconBack() {
			uni.navigateBack()
		},
		tabItem(item, index) {
			this.indexs = index
			this.goods = this.category[index].goodsList
			this.names = this.category[index].name
			console.log(this.goods)
		}
	},
	onLoad(option) {
		// console.log(option.indexs)
		this.indexs = option.indexs
		console.log(this.indexs)
		// console.log('我是onload')
	},
	//生命周期 - 创建完成（可以访问当前this实例）
	created() {

	},
	//生命周期 - 挂载完成（可以访问DOM元素）
	mounted() {
		
		// console.log('我是mouted')
		uni.request({
			url: this.$api('index/index'),
			method: 'GET',
			data: {},
			success: res => {
				console.log(res)
				this.category = res.data.newCategoryList
				this.goods = res.data.newCategoryList[this.indexs].goodsList
				this.names = res.data.newCategoryList[this.indexs].name
				// console.log(this.names)
			},
			fail: (err) => {
				console.log(err)
			},
			complete: () => {}
		});
	},
	beforeCreate() {}, //生命周期 - 创建之前
	beforeMount() {}, //生命周期 - 挂载之前
	beforeUpdate() {}, //生命周期 - 更新之前
	updated() {}, //生命周期 - 更新之后
	beforeDestroy() {}, //生命周期 - 销毁之前
	destroyed() {}, //生命周期 - 销毁完成
	activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
